import { Link } from "react-router-dom";
import { toast } from "react-toastify";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import AdminNavs from "../../components/Admin/AdminNavs";
import { deleteUser, usersList } from "../../actions/userActions";
import { BiCheck, BiEdit, BiTrash } from "react-icons/bi";
import { FaTimes } from "react-icons/fa";

const UserListScreens = ({ history }) => {
  const [keyword, setKeyword] = useState("");

  const dispatch = useDispatch();
  const { error, loading, users } = useSelector((state) => state.userList);

  const user = useSelector((state) => state.user);
  const { success: successDelete } = useSelector((state) => state.userDelete);

  useEffect(() => {
    if (user && user.isAdmin) {
      dispatch(usersList(keyword));
    } else {
      history.push("/login");
    }
  }, [dispatch, history, successDelete, user, keyword]);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (keyword.trim()) {
      history.push(`/user/${keyword}`);
    } else {
      history.push("/UserListScreens");
    }
  };

  const deleteHandler = (id) => {
    if (window.confirm("Are You Sure")) {
      dispatch(deleteUser(id));
    }
  };

  return (
    <>
      <AdminNavs />

      <header aria-label="Page Header" className="bg-gray-50">
        <div className="mx-auto max-w-screen-xl px-4 py-8 sm:px-6 lg:px-8">
          <div className="mt-8 pb-10">
            <h1 className="text-2xl font-bold text-gray-900 sm:text-3xl">
              All Users
            </h1>

            <p className="mt-1.5 text-sm text-gray-500">
              Search Products, Delete Products, Edit Products Keep it up! ðŸš€
            </p>
          </div>

          <div className="flex items-center ">
            <div className="relative hidden sm:block">
              <form  onSubmit={handleSubmit}>
                <label className="sr-only" for="search">
                  {" "}
                  Search{" "}
                </label>

                <input
                  className="h-10 w-full rounded-lg border-none bg-white pl-4 pr-10 text-sm shadow-sm sm:w-56"
                  id="search"
                  onChange={(e) => setKeyword(e.target.value)}
                  type="search"
                  name="q"
                  placeholder="Search Users..."
                />

                <button
                  type="button"
                  onClick={handleSubmit}
                  className="absolute top-1/2 right-1 -translate-y-1/2 rounded-md bg-gray-50 p-2 text-gray-600 transition hover:text-gray-700"
                >
                  <span className="sr-only">Submut Search</span>
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    className="h-4 w-4"
                    fill="none"
                    viewBox="0 0 24 24"
                    stroke="currentColor"
                    stroke-width="2"
                  >
                    <path
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                    />
                  </svg>
                </button>
              </form>
            </div>
          </div>
        </div>
      </header>

      <section className="bg-gray-50">
        <div className="container  px-5 py-10 mx-auto ">
          {loading ? (
            <p>laoding</p>
          ) : error ? (
            toast.error(error)
          ) : (
            <table className="min-w-full divide-y divide-gray-200 text-sm">
              <thead className="bg-gray-200">
                <tr>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                    ID
                  </th>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                    NAME
                  </th>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                    EMAIL
                  </th>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                    ADMIN
                  </th>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900"></th>
                </tr>
              </thead>

              <tbody className="divide-y divide-gray-200">
                {users.length === 0 ? (
                  <p>No users</p>
                ) : (
                  users.map((x) => (
                    <>
                      <tr key={x._id}>
                        <td className="whitespace-nowrap px-4 py-2 font-medium text-red-900">
                          {x._id}
                        </td>
                        <td className="whitespace-nowrap px-4 py-2 font-medium text-red-900">
                          {x.name}
                        </td>
                        <td className="whitespace-nowrap px-4 py-2 font-medium text-indigo-900">
                          {" "}
                          <a href={`/mailto:${x.email}`} target="#">
                            {" "}
                            {x.email}{" "}
                          </a>{" "}
                        </td>
                        <td className="whitespace-nowrap px-4 py-2 font-medium text-red-900">
                          {x.isAdmin ? (
                            <BiCheck color="green" size={20} />
                          ) : (
                            <FaTimes color="red" />
                          )}
                        </td>
                        <td className="whitespace-nowrap px-4 py-2 font-medium text-red-900">
                          <Link to={`/admin/user/${x._id}/edit`}>
                            <button variant="primary" className="btn-sm">
                              <BiEdit className="" color="blue" />
                            </button>
                          </Link>

                          <button
                            variant="danger"
                            className="btn-sm"
                            onClick={() => deleteHandler(x._id)}
                          >
                            <BiTrash color="red" />
                          </button>
                        </td>
                      </tr>
                    </>
                  ))
                )}
              </tbody>
            </table>
          )}
        </div>
      </section>
    </>
  );
};

export default UserListScreens;
